<template>
  <div class="bg-auto w-full">
    <div class="fade-in-right-big smooth pos-rlt p-t-lg">
      <div class="center-block w-xxl w-auto-xs p-y-xlg">
        <div class="navbar ">
          <div class="pull-center ">
            <a class="navbar-brand">
              <img src="../../static/images/logo.png" alt="."/>
            </a>
          </div>
        </div>
        <div class="p-a-md box-color r box-shadow-z1 text-color m-a">
          <div class="m-b text-sm ng-binding">
          管理后台 · 登录
          </div>
          <form @submit.prevent="login" class="form">
            <div class="md-form-group">
              <input class="md-input" v-model="user.username" name="username" ref="input">
              <label>Username</label>
            </div>
            <div class="md-form-group">
              <input type="password" class="md-input" v-model="user.password" name="password">
              <label>Password</label>
            </div>
            <div v-show="errorMsg" class="text-danger m-b-sm"> {{errorMsg}} </div>
            <button type="submit" class="btn primary btn-block p-x-md" @keyup.enter="login">
              <span v-if="loading"><i class="fa fa-spin fa-spinner"></i></span>
              <span v-else>{{ $t('nav.login') }}</span>
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import api from '../api'
import axios from 'axios'
import qs from 'qs'
export default {
  data () {
    return {
      user: {
        username: '',
        password: ''
      },
      errorMsg: '',
      loading: false
    }
  },
  watch: {
    'user': {
      handler () {
        this.errorMsg = ''
      },
      deep: true
    }
  },
  created () {
    this.$nextTick(() => {
      this.$refs.input.focus()
    })
  },
  methods: {
    login () {
      this.loading = true
      this.$http.post(api.login, qs.stringify(this.user), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(data => {
        if (data) {
          let d = new Date(data.expires_in)
          // use access_token to access APIs
          if (data.access_token) {
            this.$cookie.set('access_token', data.access_token, d.toGMTString(), '/')
            axios.defaults.headers.common['Authorization'] = `${data.token_type} ${data.access_token}`
          }
          // use refresh_token to fetch new access_token
          if (data.refresh_token) {
            this.$cookie.set('refresh_token', data.refresh_token, d.toGMTString(), '/')
          }
          this.$root.username = this.user.username
          this.$root.getMy()
          let url = this.$route.query.next
          url = url ? decodeURIComponent(url.split('?')[0]) : '/'
          this.loading = false
          this.$router.push(url)
        }
      }, error => {
        this.loading = false
        this.errorMsg = error
      })
    }
  }
}
</script>



